import React from 'react'
import { useState } from "react";
import Axios from "axios";

import { observer} from 'mobx-react';
import UserStore from '../../../stores/UserStore';



function App() {
    componentDidMount()

    const [newAddress, setNewAddress] = useState("");
    const [newTel, setNewTel] = useState("");
    const [newEmail, setNewEmail] = useState("");

    const [employeeList, setEmployeeList] = useState([]);
/*
    const getEmployees = () => {
      Axios.post("/userdata").then((response) => {
        setEmployeeList(response.data);
      });
    };*/

    async function getEmployees(){

      try {
          let res = await fetch('/userdata',{
              method: 'POST',
              headers : {
                  'Accept' : 'application/json',
                  'Content-Type' : 'application/json'
              },
              body : JSON.stringify({
                  username: UserStore.username
              })
          });

          let result = await res.json();
          if (result && result.success){
            setEmployeeList(result.info);
          }
          else if (result && result.success === false){
              
            console.log("error");

          }
      }
      catch(e){
          console.log(e);
          
      }
  }

  async function componentDidMount(){

    try{
      let res = await fetch('/isLoggedIn',{
        method : 'POST',
        headers : {
          'Accept' : 'application/json',
          'Content-Type' : 'application/json'
        }
      });

      let result = await res.json();

      if (result && result.success){
        UserStore.isLoggedIn = true;
        UserStore.username = result.username;
      }

      else{
        UserStore.isLoggedIn = false;
      }

    }
    catch(e){
      UserStore.isLoggedIn = false;
    }
  }


///

    const deleteEmployee = (id) => {
      Axios.delete(`/delete/${id}`).then((response) => {
        setEmployeeList(
          employeeList.filter((val) => {
            return val.id != id;
          })
        );
      });
    };

    const updateUserAddress = (id) => {
      Axios.put("/updateAddress", { address: newAddress, id: id }).then(
        (response) => {
          setEmployeeList(
            employeeList.map((val) => {
              return val.id == id
                ? {
                    id: val.id,
                    username: val.username,
                    tel: val.tel,
                    user_password: val.user_password,
                    email: val.email,
                    address: newAddress,
                  }
                : val;
            })
          );
        }
      );
    };

    const updateUserTel = (id) => {
      Axios.put("/updateTel", { tel: newTel, id: id }).then(
        (response) => {
          setEmployeeList(
            employeeList.map((val) => {
              return val.id == id
                ? {
                    id: val.id,
                    username: val.username,
                    tel: newTel,
                    user_password: val.user_password,
                    email: val.email,
                    address: val.address,
                  }
                : val;
            })
          );
        }
      );
    };

    const updateUserEmail = (id) => {
      Axios.put("/updateEmail", { email: newEmail, id: id }).then(
        (response) => {
          setEmployeeList(
            employeeList.map((val) => {
              return val.id == id
                ? {
                    id: val.id,
                    username: val.username,
                    tel: val.tel,
                    user_password: val.user_password,
                    email: newEmail,
                    address: val.address,
                  }
                : val;
            })
          );
        }
      );
    };

    if (!UserStore.isLoggedIn){
      return(
        <div className="app">
          <div className="container">
            Please Login...
          </div>
  
        </div>
      );
    }
    
    return ( 
      <div className="employees">
      <button class="btn btn-primary" onClick={getEmployees}>
        Show
      </button>
      <br />
      <br />
      {employeeList.map((val, key) => {
        return (
          <div className="employee card">
            <div className="card-body text-left">
              <p className="card-text">Username: {val.username}</p>
              <p className="card-text">Password: {val.user_password}</p>
              <p className="card-text">Tel Number: {val.tel}</p>

              <div className="d-flex">
                <input
                  className="form-control"
                  style={{ width: "300px" }}
                  type=""
                  placeholder="แก้ไขเบอร์โทร..."
                  onChange={(event) => {
                    setNewTel(event.target.value)
                  }}
                />
                <button className="btn btn-warning" onClick={() => {updateUserTel(val.id)}}>Update</button>

                <button className="btn btn-danger" onClick={() => {deleteEmployee(val.id)}}>Delete</button>
              </div>

              <p className="card-text">Email: {val.email}</p>

              <div className="d-flex">
                <input
                  className="form-control"
                  style={{ width: "300px" }}
                  type=""
                  placeholder="แก้ไข Email..."
                  onChange={(event) => {
                    setNewEmail(event.target.value)
                  }}
                />
                <button className="btn btn-warning" onClick={() => {updateUserEmail(val.id)}}>Update</button>

                <button className="btn btn-danger" onClick={() => {deleteEmployee(val.id)}}>Delete</button>
              </div>

              <p className="card-text">Address: {val.address}</p>
              <div className="d-flex">
                <input
                  className="form-control"
                  style={{ width: "300px" }}
                  type=""
                  placeholder="แก้ไขที่อยู่..."
                  onChange={(event) => {
                    setNewAddress(event.target.value)
                  }}
                />
                <button className="btn btn-warning" onClick={() => {updateUserAddress(val.id)}}>Update</button>

                <button className="btn btn-danger" onClick={() => {deleteEmployee(val.id)}}>Delete</button>
              </div>
            </div>
          </div>
        );
      })}
    </div>    
    )
  }

export default observer (App);