import React,{useState} from 'react';
import {ButtonGroup,ToggleButton} from 'react-bootstrap'

function App(props){



    return(
        <div>
            <p>โปรดตรวจสอบและยืนยัน</p>
<table class="table">
  <thead>
    <tr>
      <th scope="col">สินค้า</th>
      <th scope="col">จำนวน</th>
      <th scope="col">สินค้า</th>
      <th scope="col">จำนวน</th>
    </tr>
  </thead>


  <tbody>
    <tr>
      <th scope="row">โครงไก่ (19บ.)</th>
      <td>{props.item1}</td>
      <th scope="row">ขาไก่ (59บ.)</th>
      <td>{props.item2}</td>
    </tr>
    <tr>
      <th scope="row">น่องไก่ (48บ.)</th>
      <td>{props.item3}</td>
      <th scope="row">เครื่องในไก่ (48บ.)</th>
      <td>{props.item4}</td>
    </tr>
    <tr>
      <th scope="row">น่องติดสะโพก (55บ.)</th>
      <td>{props.item5}</td>
      <th scope="row">อกไก่ (48บ.)</th>
      <td>{props.item6}</td>
    </tr>
    <tr>
      <th scope="row">ปีกส่วนบน (70บ.)</th>
      <td>{props.item7}</td>
      <th scope="row">ปีกไก่ (85บ.)</th>
      <td>{props.item8}</td>
    </tr>
    <tr>
      <th scope="row">ปีกส่วนปลาย (40บ.)</th>
      <td>{props.item9}</td>
      <th scope="row">เศษเนิ้อ (45บ.)</th>
      <td>{props.item10}</td>
    </tr>
    <tr>
      <th scope="row">เศษเนิ้อติดหนัง (45บ.)</th>
      <td>{props.item11}</td>
      <th scope="row">เนื้อสันใน (50บ.)</th>
      <td>{props.item12}</td>
    </tr>
  </tbody>
</table>

<h1>ยอดเงินรวม {props.Total} บาท</h1>

<p>เลือกช่องทางชำระเงิน</p>
<ButtonGroup toggle>
        {props.radios.map((radio, idx) => (
          <ToggleButton
            key={idx}
            type="radio"
            variant="secondary"
            name="radio"
            value={radio.value}
            checked={props.radioValue === radio.value}
            onChange={(e) => props.setRadioValue(e.currentTarget.value)}
          >
            {radio.name}
          </ToggleButton>
        ))}
</ButtonGroup>

<br></br>
<button className="btn btn-outline-dark" onClick={props.sendToDB}>ยืนยันรายการสั่งซื้อ</button>
<br></br>
        </div>
        

    )

}

export default App;