import React from 'react'
import { NavLink } from 'react-router-dom'
import {Button , Dropdown,Navbar,Nav,NavDropdown} from 'react-bootstrap';




class navbar extends React.Component {
  
  render() {
    function logout(){localStorage.clear()
      console.log("babanana")
      window.location.href = "/shop"}
    return ( 
        <React.Fragment>


<Navbar bg="light" expand="lg">
  <Navbar.Brand href="#home">ChicShopReact</Navbar.Brand>
  <Navbar.Toggle aria-controls="basic-navbar-nav" />
  <Navbar.Collapse id="basic-navbar-nav">
    <Nav className="mr-auto">
      <Nav.Link href="/" >Home</Nav.Link>
      <NavDropdown title="สมาชิก" id="basic-nav-dropdown">
        <NavDropdown.Item href="/login">ล๊อกอินใช้งาน</NavDropdown.Item>
        <NavDropdown.Item href="/register">สมัครสมาชิก</NavDropdown.Item>
        <NavDropdown.Item href="/info">ข้อมูลสมาชิก</NavDropdown.Item>
        <NavDropdown.Divider />
        <NavDropdown.Item onClick={logout}>ออกจากระบบ</NavDropdown.Item>
      </NavDropdown>
      <Nav.Link href="/shop">ร้านค้า</Nav.Link>
      <Nav.Link href="/contact">ติดต่อ</Nav.Link>
    </Nav>
  </Navbar.Collapse>
</Navbar>



        </React.Fragment>
    )
  }
}
export default navbar