import { useState } from "react";
import Axios from "axios";


function App() {
    const [username, setUsername] = useState("");
    const [user_password, setPassword] = useState("");
    const [tel, setTel] = useState("");
    const [email, setEmail] = useState("");
    const [address, setAddress] = useState("");
    const [newAddress, setNewAddress] = useState("");
  
    const [employeeList, setEmployeeList] = useState([]);
  
    const getEmployees = () => {
      Axios.get("/userdata").then((response) => {
        setEmployeeList(response.data);
      });
    };
  

    
    async function addEmployee(){
      try {
        let res = await fetch('/checkRegister',{
            method: 'POST',
            headers : {
                'Accept' : 'application/json',
                'Content-Type' : 'application/json'
            },
            body : JSON.stringify({
                username: username,
                password : user_password
            })
        });

        let result = await res.json();
        if (result && result.checkNoSame){
          Axios.post("/create", {
            username: username,
            user_password: user_password,
            tel: tel,
            email: email,
            address: address,
          })
          alert(result.msg)
        }
        else{
          alert('การสมัครไม่สำเร็จ..')
          this.resetForm();
        }
    }
    catch(e){
      alert('การสมัครไม่สำเร็จ..')
      console.log(e);
      this.resetForm();
    }
    };
  
    const updateEmployeeWage = (id) => {
      Axios.put("/update", { address: newAddress, id: id }).then(
        (response) => {
          setEmployeeList(
            employeeList.map((val) => {
              return val.id == id
                ? {
                    id: val.id,
                    username: val.username,
                    tel: val.tel,
                    user_password: val.user_password,
                    email: val.email,
                    address: newAddress,
                  }
                : val;
            })
          );
        }
      );
    };
  
    const deleteEmployee = (id) => {
      Axios.delete(`/delete/${id}`).then((response) => {
        setEmployeeList(
          employeeList.filter((val) => {
            return val.id != id;
          })
        );
      });
    };
  
    return (
      <div className="App container">
        <h1>สมัครสมาชิก ChicShop</h1>
        <div className="information">
          <form action="">
            <div className="mb-3">
              <label className="form-label" htmlFor="Username">
              Username:
              </label>
              <input
                type="text"
                className="form-control"
                placeholder="ใส่ชื่อผู้ใช้งานตรงนี้"
                onChange={(event) => {
                  setUsername(event.target.value)
                }}
              />
            </div>
            <div className="mb-3">
              <label htmlFor="Password">Password:</label>
              <input
                type="text"
                className="form-control"
                placeholder="ใส่รหัสผ่านตรงนี้"
                onChange={(event) => {
                  setPassword(event.target.value)
                }}
              />
            </div>
            <div className="mb-3">
              <label htmlFor="Number">Tel Number:</label>
              <input
                type="text"
                className="form-control"
                placeholder="ใส่เบอร์ติดต่อตรงนี้"
                onChange={(event) => {
                  setTel(event.target.value)
                }}
              />
            </div>
            <div className="mb-3">
              <label htmlFor="Email">Email:</label>
              <input
                type="email"
                className="form-control"
                placeholder="name@example.com"
                onChange={(event) => {
                  setEmail(event.target.value)
                }}
              />
            </div>
            <div className="mb-3">
              <label htmlFor="Address">Address:</label>
              <input
                type="text"
                className="form-control"
                placeholder="Address (สำหรับจัดส่ง)"
                onChange={(event) => {
                  setAddress(event.target.value)
                }}
              />
            </div>
            <button onClick={addEmployee} class="btn btn-success">
              OK
            </button>
          </form>
        </div>
        <hr />
      </div>
    );
  }
  
  export default App;